import React from 'react';
import { FaFacebookF,FaTwitter,FaPinterestP,FaInstagram,FaCoffee,FaPlay} from "react-icons/fa";

const Banner = () => { 
  const [state] = React.useState({title: 'I am Shravan Ghanchi',text:'I am a full stack web programmer with long time exp in this feild.',image:'man-01.png'});
  
    return (
    <header className="header">
        <div className="container">
                 <div className="row">
                      
                      <div className="col-6">
                        <div className="header_content">
                          <div className="header_section">
                          <ul className="header_ul">
                                 <li>
                                   <FaFacebookF />
                                  </li>
                                  <li>
                                   <FaTwitter />
                                  </li>
                                  <li>
                                   <FaPinterestP />
                                  </li>
                                  <li>
                                   <FaInstagram />
                                  </li>
                                  <li>
                                   <FaCoffee/>
                                  </li>
                            </ul>
                            <h1>{state.title}</h1>
                            <p>{state.text}</p>
                            <div className="header_buttons">
                                  <a href="" className="btn btn-outline">My Portfolio</a>&nbsp;&nbsp;&nbsp;
                                  <a href="" className="btn btn-smart">
                                    <FaPlay className=""/>
                                  </a>&nbsp;&nbsp;&nbsp;
                                   
                            </div>
                          </div>
                            
                        </div>
                      </div>
                      <div className="col-6">
                            <div className="banner_img">
                              <img src={state.image} alt="man"/>
                            </div>
                      </div>
                  </div>
        </div>
      </header>
    )
}

export default Banner;
